body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.DatePickerContainer {
    max-width: 500px;
    background-color: #fff;
    padding: 10px;
    box-shadow: 0 0 0 1px rgba(63, 63, 68, 0.05), 0 1px 3px 0 rgba(63, 63, 68, 0.15)
}

.DatePickerContainer_Title {
    font-size: 1.2rem;
    color: #4f4f4f;
}

.WeekdayContainer {
    display: -webkit-flex;
    display: flex;
}

.Weekday {
    display: block;
    -webkit-flex: 1 0;
            flex: 1 0;
    padding: 0.8rem;
    background: transparent;
    font-size: 1.2 rem;
    color: #637381;
    text-align: center;
}

.Week {
    display: -webkit-flex;
    display: flex;
}

.EmptyStateDay {
    display: block;
    -webkit-flex: 1 0;
            flex: 1 0;
    width: calc(100%/7);
    margin: 0;
    padding: 0.8rem;
    border: 1px solid #dfe3e8;
}

.EmptyStateDay+.EmptyStateDay,
.EmptyStateDay+.Day {
    margin-left: -1px;
}

.Day {
    display: block;
    -webkit-flex: 1 0;
            flex: 1 0;
    width: 14.28571%;
    margin: 0;
    padding: 0.8rem;
    background: transparent;
    border: 1px solid #dfe3e8;
    border-radius: 0;
    outline: none;
    font-size: 1.2rem;
    text-align: center;
    cursor: pointer;
}

.Day_selected {
    background: #5c6ac4;
    border: 1px solid #5c6ac4;
    color: #fff;
}

.Day_hovering {
    background: #f2f2f2;
    border: 1px solid #f2f2f2;
}
.App {
    text-align: center;
}

.MainContent {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 20px;
}
